.modal {
  position: fixed;
  border-radius: 6px 6px 6px 6px;
  border: solid 1px #f5f5f5;
  z-index: 99999999;
  box-shadow: 2px 2px 10px #888888;
  padding: 0 12px 12px 12px;
  background: #ffffff;
  flex-direction: column;
}
.modal .modalBody {
  overflow: auto;
  flex: 1;
}
.title {
  background: #f5f5f5;
  min-height: 24px;
  justify-content: space-between;
}
.title:hover {
  cursor: move;
}
.upRight {
  width: 12px;
  height: 12px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
}
.upRight:hover {
  cursor: ne-resize;
}
.right {
  width: 12px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
}
.right:hover {
  cursor: e-resize;
}
.rightDown {
  width: 12px;
  height: 12px;
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 100;
}
.rightDown:hover {
  cursor: se-resize;
}
.down {
  width: 100%;
  height: 12px;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 10;
}
.down:hover {
  cursor: s-resize;
}
.downLeft {
  width: 12px;
  height: 12px;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 100;
}
.downLeft:hover {
  cursor: sw-resize;
}
.left {
  width: 12px;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
}
.left:hover {
  cursor: w-resize;
}
.leftUp {
  width: 12px;
  height: 12px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
}
.leftUp:hover {
  cursor: nw-resize;
}
